"use client"

import type { Metadata } from "next";
import { Geist, Geist_Mono } from "next/font/google";
import "./globals.css";
import Navbar from "@/components/Navbar";
import { useEffect, useState } from "react";

const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
});

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    setTimeout(() => {
      setLoading(false)
    }, 3000)
  }, [])

  return (
    <html lang="tr">
      <body
        className={`${geistSans.variable} ${geistMono.variable} antialiased  bg-[center_20px] bg-opacity-60 bg-fixed bg-cover bg-no-repeat min-h-screen bg-[url('../public/üsküdarbank.jpg')] `}
      >
        {
          loading ? 
          <div className="bg-black text-indigo-400 text-4xl w-full h-screen flex items-center justify-center ">Bu bir mafya dizisidir!</div> 
          : 
          <>
          <Navbar />
          {children}
          </>
        }
      </body>
    </html>
  );
}
